export default {
  data() {
    return {
      mealList: []
    };
  },
  onLoad() {
    // 获取会员套餐信息
    this.getMeal();
  },
  methods: {
    getMeal() {
      this.$http
        .get("/memberPackage/getMeal", { params: { id: 1 } })
        .then(res => {
          this.mealList = res.data;
        });
    }
  }
};
